"use client";

import { useEffect, useState } from "react";
import s from "./page.module.scss";

export default function CommunicationPearls() {
  const [isSmallScreen, setIsSmallScreen] = useState<boolean>(
    typeof window !== "undefined" ? window.innerWidth <= 1024 : false
  );

  useEffect(() => {
    const handleResize = () => {
      setIsSmallScreen(window.innerWidth <= 1024);
    };

    if (typeof window !== "undefined") {
      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }
  }, []);

  function scrollToSubject(subject: string) {
    const el = document.getElementById(subject);
    if (el) {
      const yOffset =
        window.scrollY +
        el.getBoundingClientRect().top -
        (isSmallScreen ? 100 : 120);
      window.scrollTo({ top: yOffset, behavior: "smooth" });
    }
  }

  return (
    <main className={s.main}>
      <div className={s.sidebar}>
        <ul className={s.sidebar__list}>
          <li>
            <button
              className="body-button"
              onClick={() => scrollToSubject("general-guidelines")}
            >
              <div>1.</div>
              <div>General Guidelines</div>
            </button>
          </li>
          <li>
            <button
              className="body-button"
              onClick={() => scrollToSubject("health-literacy")}
            >
              <div>2.</div>
              <div>Patient Health Literacy</div>
            </button>
          </li>
          <li>
            <button
              className="body-button"
              onClick={() =>
                scrollToSubject("addressing-anxiety-and-psychosocial-stress")
              }
            >
              <div>3.</div>
              <div>Addressing Anxiety and Psychosocial Stress</div>
            </button>
          </li>
          <li>
            <button
              className="body-button"
              onClick={() =>
                scrollToSubject("communication-in-the-acute-event")
              }
            >
              <div>4.</div>
              <div>Communication in the Acute Event</div>
            </button>
          </li>
          <li>
            <button
              className="body-button"
              onClick={() => scrollToSubject("communication-in-the-follow-up")}
            >
              <div>5.</div>
              <div>Communication in the Follow-up/Long-term Setting</div>
            </button>
          </li>
        </ul>
      </div>
      <div className={s.content}>
        <div className={s.content__header}>
          <h1 className="heading-xl">
            Communication
            <div className="bold"> Pearls</div>
          </h1>
          <p className={s.content__subtitle}>
            Evidence-based practices for clear, compassionate, and effective
            communication with patients that experience VTE.
          </p>
        </div>
        <div className={s.content__main} id="communication-group">
          <div className={s.content__alinea}>
            <h2 className="heading-l" id="general-guidelines">
              1. General Guidelines
            </h2>
            <ul>
              <li>
                Listen actively: Engage fully, make eye contact, and avoid
                interrupting.
              </li>
              <li>
                Be mindful of nonverbal cues like body language, facial
                expressions, and gestures.
              </li>
              <li>
                Exercise cultural competence: Cultural differences may affect
                communication.
              </li>
              <li>
                Show interest in the patient in front of you: Focus on the
                person surrounding the clot.
              </li>
              <li>
                Clarify understanding: Clear up misconceptions, e.g., a
                pulmonary embolism is NOT a stroke or heart attack.
              </li>
            </ul>
          </div>
          <div className={s.content__alinea}>
            <h2 className="heading-l" id="health-literacy">
              2. Patient Health Literacy
            </h2>
            <ul>
              <li>
                Assess baseline knowledge and determine information needs.
              </li>
              <li>Use layperson terms and avoid medical jargon.</li>
              <li>
                Chunk information: Group details to make them easier to remember
                (e.g., diagnosis info, treatment info)
              </li>
              <li>
                Utilize teach-back method: Ask patients to repeat information in
                their own words.
              </li>
              <li>Provide aids: Use written or visual materials.</li>
            </ul>
          </div>
          <div className={s.content__alinea}>
            <h2
              className="heading-l"
              id="addressing-anxiety-and-psychosocial-stress"
            >
              3. Addressing Anxiety and Psychosocial Stress
            </h2>
            <h3 className="heading-m">Normalize distress:</h3>
            <ul>
              <li>
                Acknowledge feelings of anxiety, depression, uncertainty, and
                concern.
              </li>
              <li>Offer support resources.</li>
              <li>
                Use compassionate language, e.g., “I’m here to answer
                questions.”
              </li>
            </ul>
            <h3 className="heading-m">Show empathy:</h3>
            <ul>
              <li>
                Avoid offhand comments: Do not make light of the condition or
                suggest the patient is "lucky to be alive."
              </li>
              <li>
                Solicit patient preferences and involve the patient in
                decision-making.
              </li>
            </ul>
          </div>
          <div className={s.content__alinea}>
            <h2 className="heading-l" id="communication-in-the-acute-event">
              4. Communication in the Acute Event
            </h2>
            <ul>
              <li>
                Acknowledge limits: Explain that not all information can be
                given in emergency settings.
              </li>
              <li>
                Stick to basics: Clearly state the diagnosis, immediate
                treatment, follow-up plan, and who to contact with questions.
                Explain that multiple professionals or specialists might be
                involved in VTE care going forward.
              </li>
              <li>
                Emphasize follow-up: Clearly state the importance of follow-up
                as instructed.
              </li>
              <li>
                Summarize: Provide a verbal and [preferably] written overview of
                the discussion and follow-up plan.
              </li>
            </ul>
          </div>
          <div className={s.content__alinea}>
            <h2 className="heading-l" id="communication-in-the-follow-up">
              5. Communication in the Follow-up/Long-term Setting
            </h2>
            <ul>
              <li>Explain the purpose of follow-up appointments.</li>
              <li>
                Involve caretakers: Invite primary caretakers to clinic visits.
              </li>
              <li>
                Discuss future: Empower patients by discussing “what you can do
                if…” scenarios.
              </li>
              <li>
                Clarify management: Ensure patients know who their primary VTE
                caretaker is, i.e., the clinician managing them going forward.
              </li>
              <li>
                Encourage questions: Ask patients to prepare questions with
                family/caregiver prior to follow-up appointments.
              </li>
            </ul>

            <h3 className="heading-m">Tools/resources:</h3>
            <div className={s.content__links}>
              <a
                href="https://www.vitaltalk.org/resources/quick-guides/"
                target="_blank"
              >
                VITALtalk Quick Guides
              </a>
              <a
                href="https://onlinelibrary.wiley.com/doi/10.1002/aet2.10328"
                target="_blank"
              >
                EMPATHY circle
              </a>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}
